import React, { useState } from 'react';
import "./ProductPage.css";
import { useSelector,useDispatch } from 'react-redux';
import { clearErrors,getProduct } from '../../Actions/productAction';
import Loader from '../layout/Loader/Loader';
import Product from "../layout/home/Product"
import { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import Pagination from 'react-js-pagination';
import  {Slider}  from '@material-ui/core';
import Typography from "@material-ui/core/Typography"
import { useAlert } from 'react-alert';
import MetaData from '../layout/MetaData';
const categories = [
    "Lower",
    "Shirts",
    "Shorts",
    "Jeans",
    "Laptop",
    "Tshirt",
    "Underwear",
    "Laptop",
    "Footwear",
    "Bottom",
    "Tops",
    "Attire",
    "Camera",
    "SmartPhone",
]

const ProductPage = () => {
    const dispatch = useDispatch();

    const [currentPage,setCurrentPage] =  useState(1);
    const [price,setPrice] = useState([0,25000]);
    const [category,setCategory] = useState(0);
    const [rating,setRatings] = useState([0,5]);

    const alert = useAlert();

    const {products,loading,error,productsCount,resultperPage,filteredProductsCount} = useSelector((state) => state.products)
    const {keyword} = useParams(); 
    

    const setCurrentPageNo = (e)=>{
        setCurrentPage(e);
    }

    const priceHandler = (e,newPrice)=>{
        setPrice(newPrice);
    }


    useEffect(()=>{

        if(error){
            alert.error(error);
            dispatch(clearErrors());
        }


        dispatch(getProduct(keyword,currentPage,price,category,rating));
    },[dispatch,keyword,currentPage,price,category,rating,alert,error])

    let count = filteredProductsCount;


  return (
    <>
    {loading ? <Loader/> :(
        <>
            <MetaData title = " Stock Clear-PRODUCTS"/>
            <h2 className="productsHeading">Products</h2>
            <div className="products">
                {products && products.map((product)=>(
                    <Product key ={product._id} product ={product}/>
                ))}
            </div>

            <div className="filterBox">
                   <Typography>
                    Price
                   </Typography>
                   <Slider 
                    value = {price}
                    onChange={priceHandler}
                    min = {0}
                    max = {25000}
                    valueLabelDisplay='auto'
                    steps = {100}
                    defaultValue={500}


                   />
                   <Typography>Categories</Typography>
                    <ul className="categoryBox">
                        {categories.map((category)=>(
                            
                            <li className="category-link"
                            key = {category}
                            onClick={()=>setCategory(category)}
                            >
                                {category}
                            </li>
                        )) }
                    </ul>  
                    <fieldset>
                            <Typography component="legrnd">Ratings Above</Typography>
                            <Slider
                                value = {rating}
                                onChange={(e,newRating) =>{
                                    setRatings(newRating);
                                }}
                                valueLabelDisplay='auto'
                                aria-labelledby="continuous-slider"
                                min = {0}
                                max = {5}
                            />

                           
                    </fieldset>                 
            </div>

            {resultperPage < count && (
                <div className="paginationBox">
                    <Pagination 
                        activePage={currentPage}
                        itemsCountPerPage ={resultperPage}
                        totalItemsCount = {productsCount}
                        onChange = {setCurrentPageNo}
                        nextPageText = "Next"
                        prevPageText = "Prev"
                        firstPageText= "1st"
                        lastPageText= "Last"
                        itemClass= "page-item"
                        linkClass = "page-link"
                        activeClass='pageItemActive'
                        activeLinkClass='pageLinkActive'

                    />
                </div>
            )}
        </>
    )}
    </>
  )
}

export default ProductPage
